import Layout from '../../components/Layout';
import Sidebar from './sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { selectUser } from '../../redux/userReducer';
import { useSelector } from 'react-redux';
import { useEffect, useState, createRef } from 'react';
import { web3Service } from '../../services/web3.service';
import { useNavigate } from 'react-router-dom';

const Wallet = ({
  searchBar = true,
  homeBtn = true,
  walletNav = true,
  faqBtn = false
}) => {
  const navigate = useNavigate();
  const seluser = useSelector(selectUser);
  const [walletDetail, setWalletDetail] = useState([]);
  const [transaction, setTransaction] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    getBalances();
  }, [seluser && seluser._id])

  const getBalances = async () => {    
    const btc = 0;//await getWalletBalance(0);
    const eth = await getWalletBalance(1);
    const usdt = await getWalletBalance(4);
    if (seluser && seluser._id) {
      setWalletDetail([
        {
          address: seluser.btcwalpublic, balance: btc, type: 0, name: "BTC"
        },
        {
          address: seluser.walpublic, balance: eth, type: 1, name: "ETH"
        },
        {
          address: seluser.walpublic, balance: "", type: 2, name: "BNB"
        },
        {
          address: "", balance: "", type: 3, name: "XRP"
        },
        {
          address: seluser.walpublic, balance: usdt, type: 4, name: "USDT"
        },
        {
          address: "", balance: "", type: 5, name: "CPOS"
        }
      ]);
    } else {
      setWalletDetail([]);
    }
  }

  const getWalletAddress = (addr) => {
    return addr.substr(0, 4) + "..." + addr.substr(addr.length - 4, 4)
  }

  const getWalletBalance = async (type) => {    
    const ret = await web3Service.getWalletBalance(type===0?seluser.btcwalpublic:seluser.walpublic, type);
    return ret.data.ret;
  }

  const getTransactions = async (type) => {    
    let res;
    setLoading(true);   
    setTransaction([]);
    switch (type) {
      case 0:        
        setTransaction([]);
        break;
      case 1:
        try {
          res = await web3Service.trackTransactions(seluser.walpublic, 0, type);
          setTransaction(res.data.ret);
        } catch {          
          setLoading(false);
        }
        break;
      case 2:
        break;
      case 3:
        break;
      case 4:
        try {
          res = await web3Service.trackTransactions(seluser.walpublic, 0, type);
          setTransaction(res.data.ret);
        } catch {          
          setLoading(false);
        }
        break;
      case 5:
        break;
    }
    setLoading(false);
  }

  useEffect(() => {
    getTransactions(0);   
  }, [])

  return (
    <Layout classes={'bg-[#404040] h-full'} searchBar={searchBar} walletNav={walletNav} faqBtn={faqBtn}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                {homeBtn && <FontAwesomeIcon icon={faHome} className='cursor-pointer' onClick={() => navigate('/')}/>}
                <p className='ml-2 font-bold'>Wallet</p>
              </div>
              <div className='w-full flex justify-between mt-[1rem] text-gray-100 text-xl mb-2 bold grid grid-cols-3 gap-6 px-[2rem] py-[2rem]'>
                {
                  walletDetail && walletDetail.map((detail, index) => {
                    if (detail.type === 2 || detail.type === 3 || detail.type === 5) return null;
                    return (
                      <div key={index} onClick={() => {getTransactions(detail.type);}} className='w-full h-[6rem] text-xl text-center rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 select-none cursor-pointer px-[2rem]'>
                        <div className='text-xl text-center leading-[2.4rem] text-2xl select-none font-bold'>
                          {detail.name}
                        </div>
                        <div className='text-xl text-left leading-[1.2rem] select-none'>
                          Address: {getWalletAddress(detail.address)}
                        </div>
                        <div className='text-xl text-left leading-[1.2rem] select-none'>
                          Balance: {detail.balance}
                        </div>
                      </div>
                    )
                  })
                }
              </div>
              <div className='w-full flex text-gray-100 leading-[1.8rem]'>
                <p className='w-[15%] pl-6'>Txn Hash</p>
                <p className='w-[12%] text-left'>Method</p>
                <p className='w-[15%] text-left'>Age</p>
                <p className='w-[15%] text-left'>From</p>
                <p className='w-[18%] text-left'>To</p>
                <p className='w-[10%] text-left'>Value</p>
                <p className='w-[15%] text-left'>Token</p>
              </div>
              <div className='w-full h-[calc(100vh-24rem)] carousel mt-[1rem] text-gray-100 text-xl'>
                {loading ? <div className="w-full text-center">Loading...</div> : transaction.length > 0 ? <div className='w-full h-[calc(100vh-20em)] overflow-y-auto'>
                  {
                    transaction.map((t, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 leading-[1.4rem] bg-[#333333] border-b border-[#282828]'>
                          <p className='w-[15%] text-left'>{t.blockHash.substr(0, 8) + "..."}</p>
                          <p className='w-[12%] text-left'>{t.functionName}</p>
                          <p className='w-[15%] text-left'>{t.timeStamp}</p>
                          <p className='w-[15%] text-left'>{t.from.substr(0, 8) + "..."}</p>
                          <p className='w-[18%] text-left'>{t.to.substr(0, 8) + "..."}</p>
                          <p className='w-[10%] text-left'>{(t.value / 10**18).toFixed(8)}</p>
                          <p className='w-[15%] text-left'>Token</p>
                        </div>
                      )
                    })
                  }
                </div> : <div className="w-full text-center">Empty Data</div>}
              </div>
            </div>
            {(seluser && seluser.role === 'merchant') && <Sidebar />}
        </div>
    </Layout>
  );
}

export default Wallet;