import Layout from '../../components/Layout';
import SuccessModal from '../auth/success.modal';
import Sidebar from './sidebar';
import Wallet from './wallet';
import { useSelector } from 'react-redux';
import { selectUserRole } from '../../redux/userReducer';

const Dashboard_Customer = () => {
  const userRole = useSelector(selectUserRole);

  return (
    <>
    {/* <Layout classes={'bg-[#F5F5F5] h-full'} searchBar={false}>
        <div className='flex flex-cols relative'>
            <Sidebar />
        </div>        
        <SuccessModal />
    </Layout> */}
    {userRole === 'customer' ? <Wallet searchBar={false} homeBtn={false} walletNav={false} faqBtn={true}/> : <></>}
    </>
  );
}

export default Dashboard_Customer;