import { Fragment, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Dialog, Transition } from '@headlessui/react';
import confirm from '../../asset/register/confirm.png';
import { useDispatch, useSelector } from 'react-redux';
import { registerSuccess, selectRegisterSuccess } from '../../redux/authReducer';

const SuccessModal = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const successed = useSelector(selectRegisterSuccess);

  return (
    <Transition appear show={successed} as={Fragment}>
      <Dialog
        as="div"
        onClose={() => {}}
        className="relative z-50">
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0">
          <div className="fixed inset-0 backdrop-blur-sm bg-[#FFFFFF44]" aria-hidden="true" />
        </Transition.Child>
        <div className="fixed inset-0 flex items-center bg-transparent justify-center ">
          {/* The actual dialog panel  */}
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 scale-95"
            enterTo="opacity-100 scale-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 scale-100"
            leaveTo="opacity-0 scale-95">
            <Dialog.Panel className=" max-w-full transform transition-all md:max-w-full">
              <div className="relative bg-[#404040] w-[50vw] max-w-[1000px] min-w-[380px] min-h-[460px] rounded-[1rem] drop-shadow-md p-6 text-center shadow-xl">                              
                {/* TITLE */}
                <div className=''>
                  <h1 className='text-2xl font-bold uppercase text-[#D9D9D9] select-none'>REGISTRATION COMPLETE!</h1>
                </div>
                {/* CONTENTS */}
                <div className="min-h-[310px] my-[1rem] py-[1rem]">
                  <h1 className='text-4xl font-bold uppercase text-[#D9D9D9] select-none'>WELCOME!</h1>
                  <img src={confirm} alt="confirm" className='mx-auto h-[210px] mt-6'/>
                </div>
                {/* CONTROLS */}
                <div className='flex justify-center'>
                  <div 
                    className="bg-[#802BB1] hover:bg-[#943FC5] hover:drop-shadow-lg select-none text-center text-xl text-white uppercase px-8 leading-[3rem] rounded-[2rem] cursor-pointer"
                    onClick={() => {dispatch(registerSuccess(false));}}
                    >
                    Go To Dashboard
                  </div>
                </div>         
              </div>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition>
  );
};

export default SuccessModal;